import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom"; // Importera useNavigate från react-router-dom
import axios from "axios";
import "./Header.css";
import searchIconImage from "../images/search-icon.png";
import shoppingCart from "../images/shooping-cart.png";
import profileImage from "../images/profile-image.png";
import deleteIcon from "../images/cancel.png";

const Header = ({ showTooltip }) => {
  const [showSignUp, setShowSignUp] = useState(true); // Hanterar växling mellan Sign Up och Sign In
  const [isFormVisible, setIsFormVisible] = useState(false); // Hanterar formulärvisibilitet
  const [isLoggedIn, setIsLoggedIn] = useState(false); // Håller koll på inloggningsstatus
  const [email, setEmail] = useState(""); // För e-postinmatning
  const [password, setPassword] = useState(""); // För lösenordsinmatning
  const [name, setName] = useState(""); // För namn i Sign Up
  const [errorMessage, setErrorMessage] = useState(""); // För att visa felmeddelanden

  const navigate = useNavigate(); // Använd useNavigate för att navigera mellan sidor

  // Funktion för att hantera klick på kundvagnen
  const handleCartClick = () => {
    navigate("/order-summary"); // Navigera till OrderSummary-sidan
  };

  // Funktion för att visa/dölja formuläret
  const handleProfileClick = () => {
    setIsFormVisible(true);
  };

  // Funktion för att dölja formuläret
  const handleCloseForm = () => {
    setIsFormVisible(false);
  };

  // Växla mellan Sign Up och Sign In
  const handleFormSwitch = () => {
    setShowSignUp(!showSignUp);
    setErrorMessage(""); // Rensa felmeddelande vid byte
  };

  // Funktion för att hantera registrering (Sign Up)
  const handleSignUp = async (e) => {
    e.preventDefault();
    if (!email.includes("@")) {
      setErrorMessage("Please enter a valid email.");
      return;
    }
    try {
      const response = await axios.post("http://localhost:5001/api/register", {
        name,
        email,
        password,
      });
      alert("Account created successfully! You are now logged in.");
      setIsLoggedIn(true);
      setIsFormVisible(false);
      localStorage.setItem("token", response.data.token);
    } catch (error) {
      setErrorMessage(
        error.response?.data.message || "An error occurred during registration."
      );
    }
  };

  // Funktion för att hantera inloggning (Sign In)
  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post("http://localhost:5001/api/login", {
        email,
        password,
      });
      alert("Login successful!");
      setIsLoggedIn(true);
      setIsFormVisible(false);
      localStorage.setItem("token", response.data.token);
    } catch (error) {
      setErrorMessage(
        error.response?.data.message || "Invalid email or password."
      );
    }
  };

  // Funktion för att hantera utloggning
  const handleLogout = () => {
    localStorage.removeItem("token");
    setIsLoggedIn(false);
    alert("Logged out successfully!");
  };

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      setIsLoggedIn(true);
    }
  }, []);

  return (
    <header className="header">
      <div className="left-container">
        <h1 className="company-name">UrbanEats</h1>
      </div>
      <div className="middle-container">
        <div className="list-container">
          <a href="#home">Home</a>
          <a href="#menu">Menu</a>
          <a href="#contact">Contact Us</a>
          <a href="#profile">Profile</a>
        </div>
      </div>
      <div className="right-section-header">
        <div className="cart-img-container" onClick={handleCartClick}>
          {" "}
          {/* Navigera till OrderSummary vid klick */}
          <img src={shoppingCart} className="cart-img" alt="Cart" />
          <div className={`tooltip-cart ${showTooltip ? "visible" : ""}`}></div>
        </div>
        <div className="profile-img-container" onClick={handleProfileClick}>
          <img src={profileImage} className="profile-img" alt="Profile" />
        </div>
        <div className="sign-in-button-container">
          {isLoggedIn ? (
            <button
              onClick={handleLogout}
              style={{ backgroundColor: "gray", color: "white" }}
            >
              Log out
            </button>
          ) : (
            <button
              onClick={handleProfileClick}
              style={{ backgroundColor: "red", color: "white" }}
            >
              Sign in
            </button>
          )}
        </div>
      </div>

      {isFormVisible && (
        <div>
          {showSignUp ? (
            <form className="sign-up-form" onSubmit={handleSignUp}>
              <div className="container-1-sing-up">
                <div className="sign-up-container">
                  <p>Sign Up</p>
                </div>
                <div className="delete-icon-container">
                  <img
                    src={deleteIcon}
                    alt="Delete"
                    onClick={handleCloseForm} // Stäng formuläret när du klickar på delete
                  />
                </div>
              </div>
              <label className="container-2-personal-information">
                <input
                  className="your-name-box"
                  type="text"
                  placeholder="Your Name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                />
                <input
                  className="your-email-box"
                  type="email"
                  placeholder="Your Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
                <input
                  className="your-password-box"
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </label>
              {errorMessage && <p className="error-message">{errorMessage}</p>}
              <div className="container-3-create-button">
                <button type="submit">Create Account</button>
              </div>
              <div className="container-4-already-account">
                <p className="already-account-p">Already have an account?</p>
                <p className="click-here-p" onClick={handleFormSwitch}>
                  Click here
                </p>
              </div>
            </form>
          ) : (
            <form className="sign-in-form" onSubmit={handleLogin}>
              <div className="container-1-sing-in">
                <div className="sign-in-container">
                  <p>Sign In</p>
                </div>
                <div className="delete-icon-container">
                  <img
                    src={deleteIcon}
                    alt="Delete"
                    onClick={handleCloseForm} // Stäng formuläret när du klickar på delete
                  />
                </div>
              </div>
              <label className="container-2-personal-information-sign-in">
                <input
                  className="your-email-box"
                  type="text"
                  placeholder="Your Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
                <input
                  className="your-password-box"
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </label>
              {errorMessage && <p className="error-message">{errorMessage}</p>}
              <div className="container-3-create-button">
                <button type="submit">Login</button>
              </div>
              <div className="container-4-create-account">
                <p className="create-account-p">Create a new account?</p>
                <p className="click-here-p" onClick={handleFormSwitch}>
                  Click here
                </p>
              </div>
            </form>
          )}
        </div>
      )}
    </header>
  );
};

export default Header;
