import React, { useState, useRef } from "react";

//import React from 'react';
import Header from "./components/Header"; // Importera Header-komponenten
import Welcome from "./components/Welcome";
import Filter from "./components/Filter";
import Menu from "./components/Menu";

import "./HomePage.css"; // Importera CSS-filen för stilar för HomePage

const HomePage = () => {
  const [selectedCategory, setSelectedCategory] = useState("");
  const [isFilterActive, setIsFilterActive] = useState(false);
  const [quantities, setQuantities] = useState({});

  // Skapa en referens till Menu-komponenten
  const menuRef = useRef(null);

  // Hantera kategoriändring från Welcome.jsx och scrolla till Menu
  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    setIsFilterActive(true);

    // Scrolla till Menu-komponenten när en kategori väljs
    if (menuRef.current) {
      menuRef.current.scrollIntoView({ behavior: "smooth" }); // Scrolla smidigt till Menu
    }
  };

  const handleResetFilter = () => {
    setSelectedCategory("");
    setIsFilterActive(false);
  };

  const handleAddQuantity = (id) => {
    setQuantities((prevQuantities) => ({
      ...prevQuantities,
      [id]: (prevQuantities[id] || 0) + 1,
    }));
  };

  const handleRemoveQuantity = (id) => {
    setQuantities((prevQuantities) => ({
      ...prevQuantities,
      [id]: prevQuantities[id] > 0 ? prevQuantities[id] - 1 : 0,
    }));
  };

  const hasItemsInCart = Object.values(quantities).some((qty) => qty > 0);

  return (
    <div className="home-page">
      <Header showTooltip={hasItemsInCart} />
      <Welcome onCategorySelect={handleCategorySelect} />
      <Filter onCategorySelect={handleCategorySelect} />

      {/* Referens till Menu-komponenten */}
      <div ref={menuRef}>
        <Menu
          selectedCategory={selectedCategory}
          isFilterActive={isFilterActive}
          onAddQuantity={handleAddQuantity}
          onRemoveQuantity={handleRemoveQuantity}
          quantities={quantities}
          resetCategory={handleResetFilter}
        />
      </div>
    </div>
  );
};
export default HomePage;
